import React,{Component} from 'react';
import {Card, CardImg, CardText, CardTitle} from 'reactstrap';

class News extends Component{
    constructor(props){
        super(props);
        this.state = {
            results: [],
            received: false
        };
    }

    async fetchFirst(){
        const response = await fetch("http://newsapi.org/v2/top-headlines?country=in&q=COVID&apiKey=d646bd53647f403595680230fca57293");
        const data = await response.json();
        console.log(data);
        this.setState({results: data, received: true});
        console.log(this.state.results);
    }

    componentDidMount(){
        this.fetchFirst();
    }

renderCard(newsData){
    let table = [];

    for(let i = 0; i <  newsData.articles.length; i++){
        
        let row = [];
        let Content = newsData.articles[i].content;

         if(Content !== null && newsData.articles[i].urlToImage !== null){

            let description = newsData.articles[i].description;

            if(window.innerWidth <= 500){
                if(description.length >= 80)
                    description = description.substring(0, 80);
                if(Content.length >= 80)
                    Content = Content.substring(0, 80);
                
            } else if(window.innerWidth > 500 && window.innerWidth <= 900){
                if(description.length >= 80)
                    description = description.substring(0, 100);
                if(Content.length >= 80)
                    Content = Content.substring(0, 100);
            } else {
                if(description.length >= 150)
                    description = description.substring(0, 150);
                if(Content.length >= 125)
                    Content = Content.substring(0, 125);
            }

            row.push(<CardTitle>{newsData.articles[i].source.name}</CardTitle>);
            row.push(<CardTitle>{newsData.articles[i].title}</CardTitle>);
            row.push(<CardImg height= "250px" width="100%" src={newsData.articles[i].urlToImage} ></CardImg>);
            row.push(<CardTitle>{description}</CardTitle>);
            row.push(<CardText>{Content}</CardText>);
            row.push(<CardText > <a href={newsData.articles[i].url}>Click to view full news <i className="fa fa-hand-pointer-o ml-1"></i></a> </CardText>);
            table.push( <div className="col-lg-6 "> <Card className="text-center shadow-lg effect">{row}</Card> </div> );
        }
    }

    return table;
}

render() {

    return(
            <div>
                { this.state.received ?
                    <div className="container">
                        <div className="row view overlay zoom">
                         {this.renderCard(this.state.results)}
                        </div>
                    </div>
                    :
                    <div className="loading"> Loading...  <i className="fa fa-spinner fa-spin" ></i> </div>
                }
            </div>
    );
}
}
export default News;

